<template>
  <div class="index">
    <MyHeader></MyHeader>
    <router-view></router-view>
    <MyFooter></MyFooter>
    <a class="go-top" @click="back" v-show="active==true"><i class="iconfont icon-top1"></i></a>
  </div>
</template>

<script>
import MyHeader from "@/view/home/components/header"//引入头部组件
import MyFooter from "@/view/home/components/footer"//引入底部组件
export default {
  name: 'index',
  data() {
    return {
      clientHeight:null,
      scrollTop:0,
      active:true,
      timer:null
    }
  },
  prop:[],
  components:{//定义组件
    MyHeader,
    MyFooter
  },
  methods:{
    back() {
      this.timer = setInterval(() => {
        document.documentElement.scrollTop -= 20;
        if(document.documentElement.scrollTop==0){
          clearInterval(this.timer)
        }
      },1000/60)
    }
  },
  mounted() {
    window.onscroll = function() {
      console.log(document.documentElement.scrollTop)
      if(document.documentElement.scrollTop>=300) {
        this.active = true;
      }
    }
  }
}
</script>

<style scoped>
.go-top{
  position: fixed;
  right: 5px;
  top: 70%;
  width: 40px;
  height: 40px;
  border:1px solid #ccc;
  text-align: center;
  line-height: 40px;
  border-radius: 4px;
}
.icon-top1{
  font-size:25px;
  color:#ccc;
}
</style>



